.move-free {
  @include pageGrid;

  &__description {
    font-size: 23px;
    font-weight: $font-weight-500-mobile;
    padding-top: 32px;
    margin: 0;
    grid-column: 1 / -1;

    @include onTablet {
      margin-top: 120px;
      padding: 0;
      grid-column: 4 / -1;
    }

    @include onDesktop {
      margin-top: 120px;
      padding: 0;
      grid-column: 6 / -2;
    }
  }

  &__title {
    @include h2;
    margin-top: 60px;
    grid-column: 1 / -1;

    @include onTablet {
      margin-top: 120px;
      grid-column: 1 / 4;
    }

    @include onDesktop {
      margin-top: 120px;
      grid-column: 1 / 6;
    }
   }
}
